import React from "react";
import CircularProgressbar from "react-circular-progressbar";
import TrackVisibility from "react-on-screen";
import "react-circular-progressbar/dist/styles.css";

import { AboutPageStyles } from "../../styles/AboutPage";
import { Description } from "../../styles/Description";
import { SkillWrapper } from "../../styles/SkillWrapper";
import { IconWrapper } from "../../styles/IconWrapper";
import { ImgWrapper } from "../../styles/ImgWrapper";
import skills from "./skills.json";
import skype from "./skype.svg";
import linkedin from "./linkedin.svg";
import github from "./github.svg";

const styles = {
  opacity: 0,
  transform: "translate3d(-80px,0,0)"
};

export const AboutPage = () => (
  <AboutPageStyles>
    <TrackVisibility once partialVisibility>
      {({ isVisible }) => (
        <Description style={isVisible ? null : styles}>
          I have gained 1+ years of experience in Web Development field with
          various projects. I am always trying to write as simple code as
          possible and divide it into logical parts. Mostly Self-Tought.
          Designing, coding and modifying websites, from layout to function and
          according to a client's specifications. Strive to create visually
          appealing sites that feature user-friendly design and clear
          navigation. The more challenges your company can provide me with, the
          more efficient my workability would be.
          <IconWrapper>
            <a href="skype:live:a0a20988b2cfaddb?call" target="_blank">
              <ImgWrapper src={skype} alt="Skype" />
            </a>

            <a
              href="https://www.linkedin.com/in/alimzhan-aigarayev/"
              target="_blank"
            >
              <ImgWrapper src={linkedin} alt="LinkedIn" />
            </a>

            <a href="https://github.com/aaigarayev" target="_blank">
              <ImgWrapper src={github} alt="GitHub" />
            </a>
          </IconWrapper>
        </Description>
      )}
    </TrackVisibility>

    <SkillWrapper>
      {skills.map(skill => {
        return (
          <React.Fragment key={skill.skill}>
            <TrackVisibility once partialVisibility>
              {({ isVisible }) => (
                <CircularProgressbar
                  percentage={isVisible ? skill.percent : 0}
                  text={skill.skill}
                />
              )}
            </TrackVisibility>
          </React.Fragment>
        );
      })}
    </SkillWrapper>
  </AboutPageStyles>
);
