import styled, { keyframes, css } from "styled-components";

const slideInDown = keyframes`
    0% {
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
        visibility: visible;
        opacity: 0;
    }

    70% {
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
        visibility: visible;
        opacity: 0;
    }

    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
`;

const slideInUp = keyframes`
    0% {
        -webkit-transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
        visibility: visible;
        opacity: 0;
    }

    70% {
        -webkit-transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
        visibility: visible;
        opacity: 0;
    }

    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
`;

export const TitleWrapper = styled.div`
  position: ${props => (props.primary ? "absolute" : "relative")};
  display: ${props => (props.primary ? "-ms-grid" : "block")};
  display: ${props => (props.primary ? "grid" : "block")};
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
  top: ${props => (props.primary ? "45%" : "0")};
  left: 0;
  right: 0;
  color: #fff;
  text-align: center;
  font-weight: 300;
  font-size: ${props => (props.primary ? "50px" : "20px")};
  letter-spacing: 5px;
  text-transform: uppercase;
  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: ${props => (props.primary ? slideInDown : slideInUp)}
    ${props => (props.primary ? "1.5s" : "2.5s")};

  @media (max-width: 980px) {
    grid-template-columns: 1fr;
    font-size: ${props => (props.primary ? "90px" : "40px")};
  }

  @media (min-width: 981px) {
    ${props => (props.primary ? "" : "grid-column-start: 1;")}
    ${props => (props.primary ? "" : "grid-column-end: 3;")}
  }

  @media (max-width: 700px) {
    font-size: ${props => (props.primary ? "4em" : "20px")};
  }
`;

export const Title = styled.h1`
  text-align: ${props => (props.primary ? "right" : "left")};
  font-size: 1em;
  text-transform: uppercase;
  opacity: 1;
  margin: 0;
  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (max-width: 980px) {
    text-align: center;
  }
`;
