import React from 'react';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

import { TimelineWrapper } from '../../styles/TimelineWrapper';
import { TimelineTitle } from '../../styles/TimelineTitle';
import { WorkIcon } from '../../styles/WorkIcon';
import jobs from './experience.json';

export const Timeline = () => (
    <TimelineWrapper>
        <TimelineTitle>Experience</TimelineTitle>
        <VerticalTimeline>
            {jobs.map((job) => {
                return (

                    <React.Fragment key={job.title}>

                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            date={job.year}
                            iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                            icon={<WorkIcon />}
                        >
                            <h3 className="vertical-timeline-element-title">{job.title}</h3>
                            <h4 className="vertical-timeline-element-subtitle">{job.company}</h4>
                            <p>
                                {job.description}
                            </p>
                        </VerticalTimelineElement>

                    </React.Fragment>

                )
            })}
        </VerticalTimeline>
    </TimelineWrapper>
)